import { mapState } from 'vuex';
export default{
    props: ['shopTypeList'],
    data() {
        return {
        };
    },
    methods: {
        goSearch(item) {
            this.$router.push({path: '/shops',
                query: {
                    'searchTypeName': item.typeZh, // 首页选择相关行业时 的行业类型 默认为0  全部类型
                    'searchTypeValue': item.typeValue // 首页选择相关行业时 的行业名称   用来传递
                }});  // 跳转到商户搜索页
        }
    },
    computed: {
        ...mapState([
            'global'
        ])
    }
};



