import {mapState} from 'vuex';
export default{
    props: ['adList'],
    data() {
        return {
        };
    },
    methods: {
        handleAdClick: function (ad) {
            window.location.href = ad.link;
        }
    },
    computed: {
        ...mapState(['global'])
    }
};



